.work {
    padding: 3rem 0;
    /* background-color: dodgerblue; */

    .work-headline {
        h3 {
            font-size: 3rem;
            font-weight: 700;
            line-height: 1.5;
            letter-spacing: -1.5px;
        }

        p {
            margin-top: 1rem;
            color: var(--secondary-color);
            font-family: var(--main-font);
            font-size: 1.25rem;
            letter-spacing: -0.5px;
            line-height: 1.5;
        }
    }

    .work-projects {
        margin-top: 3rem;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 1rem 2rem;
    }
}

@media (max-width: 1100px) {
	.work .work-headline {
        h3 {
            font-size: 38px;
        }

        p {
            font-size: 1.125rem;
        }
    }
}
  
@media (max-width: 768px) {
	.work .work-headline {
        h3 {
            font-size: 2rem;
        }

        p {
            font-size: 1rem;
        }
    }
}

@media (max-width: 370px) {
    .work .work-headline {
        text-align: center;
        
        h3 {
            font-size: 1.5rem;
        }

        p {
            font-size: 0.875rem;
        }
    }
}