.experience {
    padding: 3rem 0;
    background-color: var(--experience-color);
    
    .experience-headline h3 {
        font-size: 48px;
        font-weight: 700;
        line-height: 1.5;
        letter-spacing: -1.5px;
    }

    .experience-main {
        margin-top: 2rem;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 1rem 2rem;
    }
}

.experience-card {
    padding: 20px 0;
    // border: 2px solid #efefef;
    // border-radius: 4px;

    h4 a {
        color: var(--secondary-color);
        font-size: 24px;
        font-weight: 500;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    p {
        margin-top: 10px;
        font-size: 14px;
        color: var(--secondary-color);
        font-weight: 400;
    }
}

@media (max-width: 1100px) {
	.experience .experience-headline h3 {
        font-size: 38px;
    }

    .experience-card h4 a {
        font-size: 18px;
    }
}
  
@media (max-width: 768px) {
	.experience .experience-headline h3 {
        font-size: 2rem;
    }

    .experience-card {
        margin-bottom: 0;
        
        h4 a {
            font-size: 1rem;
        }

        p {
            font-size: 12px;
        }
    }

}

@media (max-width: 370px) {
    .experience {
        padding: 30px 0 20px;

        .experience-main {
            margin-top: 10px;
        }

        .experience-headline h3 {
            font-size: 24px;
            text-align: center;
        }
    }
    
    .experience-card {
        width: 100%;
        margin-left: 0 !important;
        margin-bottom: 0;
        text-align: center;
    }
}